import React, {useContext, useEffect, useMemo} from "react";
import styled from "styled-components";
import {HeaderWidget} from "./widget/header";
import {NavLink, Route, Switch, useParams} from "react-router-dom";
import {WidthStyle} from "../../widget/style/width_style";
import {Grid, PropTypes} from "@material-ui/core";
import {NftBuyItem} from "./widget/nft_buy_item";
import {NftActivityPage} from "./activity/nft_activity";
import {NftPool} from "./pool/nft_pool";
import {CollectionContext, useCollection} from "../../provider/collection_provicer";
import * as R from "ramda";
import {compose, flatten, map, path} from "ramda";
import {usePairs} from "./hook/usePairs";
import {HttpStatus, useWait} from "../../utils/wait_fn";
import {HttpServer} from "../../net/http_server";
import {LoadingWidget} from "../../widget/base/loading_widget";
import {ShoppingType} from "../shopping_cart/type";
import {SudoMetaData} from "../../model/sudo_meta_data";
import {useActivity} from "./hook/useActivity";
import DumpImg from "../../assets/images/dump.png";
import SweepImg from "../../assets/images/sweep.png";
import {ModalContext} from "../../provider/model_provider";
import {DumpModel} from "./model/dump_mode";
import BigNumber from "bignumber.js";
import {PoolType} from "./pool/nft_pool_item";


export const CollectionMain: React.FC = () => {

    let {id} = useParams<{ id: string, type: string }>();
    const {data, request, status} = useWait(() => HttpServer.getCollectionById(id))


    useEffect(() => {
        request('')
    }, [])


    const collections = useMemo(() => {
        if (!data) return {};
        return data[0]
    }, [data])

    const {pairsItems, nftMeta} = usePairs()

    if (status === HttpStatus.wait || !data) {
        return <LoadingWidget/>
    }

    return (
        <CollectionContext.Provider value={{
            nftMeta,
            collections,
            sudo: pairsItems
        }}>
            <CollectionBody/>
        </CollectionContext.Provider>

    );
}

const CollectionBody: React.FC = () => {
    const {sudo, collections, nftMeta} = useCollection();
    let {id, type} = useParams<{ id: string, type: string }>();
    const [sellList, buyList] = useMemo(() => {
        return compose(
            R.partition((ev: any) => parseInt(ev.type) === PoolType.token),
            R.sort((a: any, b: any) => new BigNumber(a.spotPrice).minus(b.spotPrice).toNumber()),
            Object.values,
        )(sudo)
    }, [sudo])

    const pools: any = useMemo(() => {
        return R.compose(R.map(R.head),
            R.values,
            R.groupBy((student: SudoMetaData) => {
                return student.id;
            }))(Object.values(sudo));
    }, [sudo])
    let {nftEvents, nftMeta: poolNfts} = useActivity()
    const {onPresent} = useContext(ModalContext);

    if (!collections || !sudo || !nftMeta) {
        return <div/>
    }

    return <CollectionMainStyle>
        <HeaderWidget collections={collections}/>

        <div className="bar">
            <div className="line">
                <div className={'sweep'}>
                    {type === 'buy' && <div onClick={() => {
                        console.log(nftMeta, buyList)
                        onPresent(<DumpModel nftMeta={nftMeta} listPairs={buyList}/>)
                    }}><img src={SweepImg} alt=""/>Sweep</div>}

                    {type === 'sell' && <div onClick={() => {
                        onPresent(<DumpModel nftMeta={nftMeta} listPairs={buyList}/>)
                    }}><img src={DumpImg} alt=""/>Dump</div>}
                </div>
                <StyledLink to={`/collection/buy/${id}`}>Buy <span>{buyList.length}</span></StyledLink>
                <StyledLink to={`/collection/sell/${id}`}>Sell <span>{sellList.length}</span></StyledLink>
                <StyledLink to={`/collection/activity/${id}`}>Activity</StyledLink>
                <StyledLink to={`/collection/pools/${id}`}>Pools <span>{pools.length}</span></StyledLink>
            </div>
        </div>
        <div className="list_data">
            <Switch>
                <Route path="/collection/buy/:id">
                    <Grid container spacing={4}>
                        {
                            buyList.length === 0 && <NaData>empty</NaData>
                        }
                        {
                            buyList.map((ev) => {
                                let img = path<string>([ev.uqid, 'metadata', 'image'], nftMeta)
                                if(!img){
                                    img = path<string>([ev.uqid, 'tokenUri', 'gateway'], nftMeta)
                                }
                                let name = path<string>([ev.uqid, 'metadata', 'name'], nftMeta)
                                return (
                                    <Grid item xs={6} sm={3} md={2} key={`buy_${ev.uqid}`}>
                                        <NftBuyItem data={{...ev, imgUrl: img, symbol: name}}/>
                                    </Grid>
                                );
                            })
                        }
                    </Grid>
                </Route>
                <Route path="/collection/sell/:id">
                    {
                        sellList.length === 0 && <NaData>empty</NaData>
                    }
                    <Grid container spacing={4}>
                        {
                            sellList.map((ev) => {
                                let img = path<string>([ev.uqid, 'metadata', 'image'], nftMeta)
                                let name = path<string>([ev.uqid, 'metadata', 'name'], nftMeta)
                                return (
                                    <Grid item xs={6} sm={3} md={2} key={`sell_${ev.uqid}`}>
                                        <NftBuyItem data={{...ev, imgUrl: img, symbol: name}}/>
                                    </Grid>
                                );
                            })
                        }
                    </Grid>
                </Route>

                <Route path="/collection/activity/:id"
                       children={<NftActivityPage nftEvents={nftEvents} nftMetas={poolNfts}/>}/>
                <Route path="/collection/pools/:id" component={NftPool}/>


            </Switch>
        </div>
    </CollectionMainStyle>
}

