import TisIcon from "../../../assets/images/tis_img.png";


interface InputInterFace {
    label: string,
    unit?: string,
    tis?: string[],
    onChange?: (e: any) => void,
    placeholder: string,
    setValue?: (ev: string) => void,
    inputRef: React.Ref<any>,
    errors: any;
    name?: string,
    buttonText?: string,
    type?: string,
    sunTitle?: any,
    lastTis?: string
}


export const LabelInput: React.FC<InputInterFace> = ({
                                                         label,
                                                         placeholder,
                                                         unit,
                                                         inputRef,
                                                         errors,
                                                         name,
                                                         type,
                                                         onChange,
                                                         sunTitle = '',
                                                         lastTis = '',
                                                         tis,
                                                         children,
                                                     }) => {


    return (
        <>
            <LabelStyle>
                <p className={'left'}>
                    {label}


                    {tis && <Tooltip title={tis[0]} placement="top" arrow aria-label="add">
                        <img className={'tis_icon'} src={TisIcon} alt=""/>
                    </Tooltip>}
                </p>
                <p className='right'>
                    {lastTis}
                </p>


            </LabelStyle>

            <FlexStyle>
                <InputStyle>
                    <TextField name={name || label}
                               error={Boolean(errors[name || label])}
                               placeholder={placeholder}
                               unselectable={'off'}
                               fullWidth={true}
                               inputRef={inputRef}
                               type={type}
                               onChange={(ev) => onChange && onChange(ev)}
                    />
                    {
                        unit ?
                            <span className={'unit'}>{unit}</span> : children
                    }
                </InputStyle>


            </FlexStyle>
            <HelperTextStyle>
                <p className={'err'}>{errors[name || label] ? errors[name || label].message : ''}</p>
                {sunTitle}
            </HelperTextStyle>
        </>
    );
}

