import styled from "styled-components";
import React, {useMemo} from "react";
import {BarWidget} from "./widget/type_bar";
import {NftItemCart} from "./widget/nft_shopping_item";
import {ShoppingType} from "./type";
import {useShoppingCart} from "../../provider/shopping_cart_provider";
import {compose} from "ramda";
import * as R from "ramda";
import {PoolType} from "../collection/pool/nft_pool_item";
import BigNumber from "bignumber.js";
import {SudoMetaData} from "../../model/sudo_meta_data";
import ethImg from "../../assets/images/index_eth.png"

export const ShoppingCart: React.FC = () => {
    const {pairsMap} = useShoppingCart()
    const [sellList, buyList] = useMemo(() => {
        return compose(
            R.partition((ev: any) => parseInt(ev.type) === PoolType.token),
            R.sort((a: any, b: any) => new BigNumber(a.averagePrice).minus(b.averagePrice).toNumber()),
            Object.values,
        )(pairsMap)
    }, [pairsMap])

    return (<ShoppingCartStyle>
        <div className="box">
            <BarWidget list={buyList} type={ShoppingType.buy}/>
            {
                buyList.length === 0 && <p>N/A</p>
            }

            {
                buyList.map((ev: SudoMetaData) => {
                    return <NftItemCart uqid={ev.uqid}/>
                })
            }
            <div style={{height: "20px"}}/>
            <BarWidget list={sellList} type={ShoppingType.sell}/>
            {
                sellList.length === 0 && <p>N/A</p>
            }
            {
                sellList.map((ev: SudoMetaData) => {
                    return <NftItemCart uqid={ev.uqid}/>
                })
            }
        </div>

        <div className={'bottom'}>
            <div className="tis">
                <h6>Net Sum:</h6>
                <div>
                    <img src={ethImg} alt=""/> <span>50.7</span>
                </div>
            </div>
            <p className="btn">
                CONFIREM
            </p>
        </div>


    </ShoppingCartStyle>);
}


const ShoppingCartStyle = styled.div`


  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .bottom {
    padding: 10px;

    .tis {
      display: flex;
      justify-content: space-between;
      align-items: center;

      width: 80%;
      margin: auto;
      margin-bottom: 20px;


      h6, div {
        color: #FFFFFF;
      }

      h6 {
        font-weight: 400;
        font-size: 18px;
        line-height: 18px;
        display: flex;
        align-items: center;
        letter-spacing: -0.15em;
      }

      div {
        display: flex;
        align-items: center;


        img {
          display: block;
          width: 20px;
          height: 20px;
          margin-right: 10px;
        }

        span {
          font-style: normal;
          font-weight: 400;
          font-size: 30px;
          line-height: 30px;
          text-align: center;
        }
      }
    }

    .btn {
      cursor: pointer;
      font-weight: 400;
      font-size: 20px;
      line-height: 20px;
      text-align: center;
      letter-spacing: -0.14em;
      color: #000000;
      width: 60%;
      padding: 8px 0;
      margin: 0 auto;
      background: #FFFFFF;
      border: 2px solid #000000;
      border-radius: 12px;
    }
  }

  width: 430px;
  padding: 10px;
  height: 100%;
  background: #1C1C1C;


  .box {
    flex: 1;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid #8D909A;
    overflow-y: scroll;

  }
`
