import React, {useContext, useMemo, useState} from "react";
import styled from "styled-components";
import {createStyles, Grid, makeStyles, Slider, Theme, withStyles} from "@material-ui/core";
import {NftMateMap} from "../type";
import {SudoMetaData} from "../../../model/sudo_meta_data";
import {ActivityItem} from "../activity/activity_item";
import {path, reduce, slice} from "ramda";
import {NftMetadata} from "../../../model/nft_metadata";
import ethImg from "../../../assets/images/pool_eth.png";
import BigNumber from "bignumber.js";
import {getBalanceFormat} from "../../../utils/formatBalance";
import deleteimg from "../../../assets/images/deletc_icon.png"
import {ModalContext} from "../../../provider/model_provider";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {},
        margin: {
            height: theme.spacing(3),
        },
    }),
);


const PrettoSlider = withStyles({
    root: {
        color: '#FFFFFF',
        height: 8,
    },
    thumb: {
        height: 24,
        width: 24,
        backgroundColor: '#fff',
        border: '2px solid currentColor',
        marginTop: -8,
        marginLeft: -12,
        '&:focus, &:hover, &$active': {
            boxShadow: 'inherit',
        },
    },
    active: {},
    valueLabel: {
        left: 'calc(-50% + 4px)',
    },
    track: {
        height: 8,
        borderRadius: 4,
    },
    rail: {
        height: 8,
        borderRadius: 4,
    },
})(Slider);


type DumpModelProps = {
    listPairs: SudoMetaData[],
    nftMeta: NftMateMap,
}
export const DumpModel: React.FC<DumpModelProps> = ({listPairs, nftMeta}) => {


    const classes = useStyles();
    const [value, setValue] = useState(0);
    const buyPairs = useMemo(() => {
        return slice(0, value)(listPairs)
    }, [value])

    const {onDismiss} = useContext(ModalContext);

    const total = useMemo(() => {
        return reduce((acc, cur: SudoMetaData) =>
            acc.plus(cur.spotPrice).plus(cur.delta).plus(cur.fee), new BigNumber(0))(buyPairs)
    }, [buyPairs])
    return (

        <DumpModelStyle className={classes.root}>
            <img onClick={() => {
                onDismiss()
            }} src={deleteimg} alt="" className="delete"/>
            <div className="model_title">
                <h1>Dump Mode</h1>
                <h3>Select number of NFTs to sweep.</h3>
                <h4>Total items: <span>{value}</span></h4>

                <PrettoSlider
                    aria-labelledby="continuous-slider"
                    valueLabelDisplay="off"
                    defaultValue={0}
                    onChange={(_, value) => {

                        if (typeof value === 'number') {
                            let _v = Math.ceil(listPairs.length / 100 * value);
                            if (_v > listPairs.length) {
                                _v = listPairs.length;
                            }
                            setValue(_v)
                        }
                    }}
                />
            </div>


            <div className={'center_box'}>
                <Grid container spacing={2}>
                    {
                        buyPairs.map((ev) => {
                            let item: NftMetadata = nftMeta[ev.uqid]
                            return <Grid item xs={4} md={3}>
                                <ActivityItem data={{
                                    symbol: ev.nftId,
                                    imgUrl: path(['metadata', 'image'], item),
                                    addr: ev.id
                                }}/>
                            </Grid>
                        })
                    }
                </Grid>
            </div>

            <div className="pt total">
                <span>total</span>
                <h4><img src={ethImg} alt=""/>{getBalanceFormat(total)}</h4>
            </div>

            <div className="confirm">
                Confirm
            </div>


        </DumpModelStyle>

    )
}

