import React, {useContext} from "react";
import styled from "styled-components";
import {useForm} from "react-hook-form";
import {LabelInput} from "../../updata_pool/widget/label_input";
import {MoralisData} from "../../../model/moralis_data";
import Question from "../../../assets/images/question-mark.svg"
import {ModalContext} from "../../../provider/model_provider";


type NftPoolProps = {
    nfts: MoralisData[],
    setMoralis: Function
}

export const ListTokenModel: React.FC<NftPoolProps> = (props) => {


    const {register, errors} = useForm();
    const {onDismiss} = useContext(ModalContext);


    return (
        <NftPollStyle>
            <div className="model_title">
                <h3>Select token</h3>
            </div>

            <div className="pt">
                <LabelInput errors={errors}
                            inputRef={register({})}
                            label={''}
                            name={'Price'}
                            placeholder={'Only Eth is currently enable'}
                >
                </LabelInput>
            </div>

            <div className="list">
                {props.nfts.map((ev) => {

                    return <NftItemStyle onClick={() => {

                        props.setMoralis(ev)
                        onDismiss()
                    }}>
                        <div className={'img'}>
                            <img src={Question} onError={(event) =>  Question}
                                 alt="-"/>
                        </div>
                        <div>
                            <h2>{ev.name}</h2>
                            <h4>{ev.symbol}</h4>
                        </div>

                    </NftItemStyle>
                })}
            </div>


        </NftPollStyle>
    )
}


const NftItemStyle = styled.div`
  display: flex;
  padding: 10px 10px;
  background: #52565F;
  border: 1px solid #323841;
  margin-bottom: 20px;
  border-radius: 10px;
  cursor: pointer;

  .img {
    margin-right: 10px;

    img {
      display: block;
      width: 30px;
      height: 30px;
    }
  }

  div {
    h2 {
      font-family: 'Skinny';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 18px;
      color: #FFFFFF;
      letter-spacing: 0;
    }

    h4 {
      font-family: 'Skinny';
      font-style: normal;
      font-weight: 400;
      line-height: 15px;
      font-size: 15px;
      color: #333;
      letter-spacing: 0;
    }
  }

`

const NftPollStyle = styled.div`

  .list {
    padding: 20px 0;

    p {
      color: white;
    }
  }

  width: 60%;
  margin: 0 auto;
  background-color: #191b1f;
  border-radius: 20px;
  border: 1px solid #2C2F36;
  padding: 0 20px;
  box-sizing: border-box;

  .model_title {
    padding: 17px 0;
    border-bottom: 2px solid #8D909A;
    text-align: center;

    h3 {
      font-weight: 400;
      font-size: 20px;
      line-height: 20px;
      letter-spacing: -0.14em;
      color: #FFFFFF;
      margin-bottom: 20px;
    }

    h4 {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;
      color: #52565F;
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        display: block;
        width: 35px;
      }
    }

  }

  .pt {
    margin-top: 20px;
  }

`
