import {useCallback, useEffect, useState} from "react";
import {forEach} from "ramda";

import {HttpServer} from "../../../net/http_server";
import * as R from "ramda";
import {NftMateMap} from "../../collection/type";
import {RootMyPoolData} from "../../../model/my_pool_data";
import {MoralisData} from "../../../model/moralis_data";


type useActivityProps = {
} & RootMyPoolData
export const useActivity = ({nftIds, nft}: useActivityProps) => {

    const [nftMeta, setNftMeta] = useState<NftMateMap>({});


    const handeGetMateData = useCallback(async () => {


        forEach(async (tokenId) => {
            let key = `${nft}_${tokenId}`
            let _meta: any = await HttpServer.alchemyapi(nft, tokenId || '0')
            nftMeta[key] = _meta;
            let _data: any = {}
            _data[key] = _meta;
            setNftMeta(R.mergeDeepRight(nftMeta, _data))
        }, nftIds)

    }, [nftMeta, nftIds])


    useEffect(() => {
        handeGetMateData()
    }, [])


    return {nftMeta}

}
