import styled from "styled-components";
import React, {useState} from "react";
import {BalanceInput} from "./balance_input";
import {PoolType} from "../../collection/pool/nft_pool_item";
import {useUpDataPool} from "../../../provider/updata_pool_provicer";
import {useDepositETH} from "../../../server/factory";
import {useSendModal} from "../../../widget/modal/send_modal";
import {UpdataLoadingBtn} from "./loading_btn";
import {useWithdrawETH} from "../../../server/pair";

export const TokenBalanceInput: React.FC = () => {

    const {sudo: sudoData, collections} = useUpDataPool()
    const [amount, setAmount] = useState('0')
    const handleDepositETH = useDepositETH(sudoData?.id || '')
    const {onSend, status,} = useSendModal(handleDepositETH)

    const handleWithdrawETH = useWithdrawETH(sudoData?.id || '')
    const {onSend: sendWithdraw, status: withdraw,} = useSendModal(handleWithdrawETH)

    if (!collections || !sudoData) {
        return <div/>
    }

    return (
        <BalanceWidget>
            <BalanceInput
                label={'Token Balance'}
                name={'balance'}
                placeholder={'1'}
                unit={`ETH`}
                imgUrl={'eth'}
                onChange={(ev) => {
                    setAmount(ev.target.value)
                }}
                disabledText={parseInt(sudoData.type) === PoolType.nft ? ["NFT my_pool", "has No Token Balance"] : undefined}
            >
                <>
                    <UpdataLoadingBtn isLoading={status} onClick={() => {
                        onSend(amount)
                    }}>
                       <p className={'deposit'}>Deposit</p>
                    </UpdataLoadingBtn>
                    <UpdataLoadingBtn isLoading={withdraw} onClick={() => {
                        sendWithdraw(amount)
                    }}>
                        <p className={'deposit'}>Withdraw</p>
                    </UpdataLoadingBtn>
                </>
            </BalanceInput>
        </BalanceWidget>
    );
}


export const BalanceWidget = styled.div`
  margin-top: 10px;
  .deposit{
    font-size: 12px;
  }

`
