import React, {useMemo} from "react";
import styled from "styled-components";
import Roy from "../../../assets/images/question-mark.svg"
import {SudoMetaData} from "../../../model/sudo_meta_data";
import Jazzicon from '@raugfer/jazzicon';
import indexEth from "../../../assets/images/index_eth.png"
import BigNumber from "bignumber.js";
import {getBalanceNumber} from "../../../utils/formatBalance";
import {useShoppingCart} from "../../../provider/shopping_cart_provider";
import {useCollection} from "../../../provider/collection_provicer";


type NftBuyItemProps = {
    data: {
        id: string,
        uqid: string,
        nftIds: any[],
        averagePrice: string;
        nftId?: string,
        spotPrice?: string,
        delta?: string,
        imgUrl?: string,
        symbol?: string
    },
    type?: 'max' | 'min'
}
export const NftBuyItem: React.FC<NftBuyItemProps> = ({data, type = 'max'}) => {
    let {uqid} = data;
    const buildDataUrl = useMemo(() => {
        return 'data:image/svg+xml;base64,' + btoa(Jazzicon(data.id));
    }, [data])
    const {sudo, nftMeta} = useCollection();
    const {addShopping, pairsMap, deleteShopping} = useShoppingCart()
    return (
        <NftBuyItemStyle className={`${type} ${pairsMap[uqid] && 'ac'}`} onClick={() => {
            if (pairsMap[uqid]) {
                deleteShopping(uqid)
                return;
            }
            addShopping(nftMeta[uqid], sudo[uqid])
        }}
        >
            <div className="top">
                {
                    data.spotPrice && <img src={buildDataUrl} alt="" className="addr"/>
                }

                <img src={data.imgUrl || Roy} alt="" className="nft_img"/>
            </div>
            <div className="bottom">
                {
                    type === 'max' && <>
                        <p className={'nftId_'}>#{data.nftId}</p>
                        <p className={'nftId'}>{data.symbol}</p>
                    </>
                }

                {
                    type === 'min' && <>
                        <p className={'nftId'}>{data.symbol}</p>
                    </>
                }

                {
                    data.spotPrice && <>
                        <div className="price">
                            <h3>Price</h3>
                            <h4><img src={indexEth}
                                     alt=""/> {getBalanceNumber(data.averagePrice)}
                            </h4>
                            {/*<div className="lastSale">*/}
                            {/*    Last Sale: <h4><img src={indexEth} alt=""/>7.4</h4>*/}
                            {/*</div>*/}
                        </div>
                    </>
                }


            </div>


        </NftBuyItemStyle>
    );
}

