import React from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {LabelInput} from "../widget/label_input";
import ethImg from "../../../assets/images/pool_eth.png";
import {useForm} from "react-hook-form";
import {useSendModal} from "../../../widget/modal/send_modal";
import {NftPoolProps} from "../index";
import {useDepositETH} from "../../../server/factory";
import {LoadingBtn} from "../../../compones/loading_btn";
import {useMulticall} from "../../../server/pair";

type UpdateModelProps = {
    onDismiss: Function
} & NftPoolProps
export const UpdateModel: React.FC<UpdateModelProps> = ({sudo}) => {

    const {register, errors, getValues} = useForm();
    const handleMulticall = useMulticall(sudo?.id || '');
    const {onSend, status,} = useSendModal(handleMulticall)

    return (
        <WapStyle>
            <div className="model_title">
                Edit Price and Delta
            </div>
            <CellBottomStyle container>
                <div className="pt">
                    <LabelInput errors={errors}
                                inputRef={register({})}
                                lastTis={'Floor Price: 1.000 ETH'}
                                label={'Current Price'}
                                name={'price'}
                                placeholder={""}
                    >
                        <img className={'ethImg'} src={ethImg} alt=""/>
                    </LabelInput>
                </div>
                <div className="pt">
                    <LabelInput errors={errors}
                                inputRef={register({
                                    required: 'Please enter a number > 0'
                                })}
                                label={'Delta'}
                                name={'delta'}
                                placeholder={""}
                                unit={"%"}
                    />
                </div>

            </CellBottomStyle>

            <div className={'bottom'}>
                <LoadingBtn isLoading={status} onClick={() => {
                    let values = getValues();
                    onSend(values)
                }}>
                    <h4>Update</h4>
                </LoadingBtn>
            </div>
        </WapStyle>
    )
}


const WapStyle = styled.div`

  width: 60%;
  margin: 0 auto;
  background-color: #191b1f;
  border-radius: 20px;
  border: 1px solid #2C2F36;
  padding: 10px 20px;
  box-sizing: border-box;

  .bottom {
    display: flex;
    justify-content: center;

    > div {
      width: 50%;
    }
  }

  .model_title {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-align: center;
    color: #D9D9D9;
    border: 1px solid #191B1F;
  }
`


