import React, {useEffect, useMemo, useState} from "react";
import {Grid} from "@material-ui/core";
import styled from "styled-components";

import {CellCollectionsWidget} from "./cell_collections";
import {CryptoWidget} from "./crypto_simeji";
import {HttpStatus, useWait} from "../../../utils/wait_fn";
import {HttpServer} from "../../../net/http_server";
import {LoadingWidget} from "../../../widget/base/loading_widget";
import {CellCollectionsData} from "../type";
import {slice} from "ramda";
import {useStore} from "effector-react";
import {$getCollections} from "../store";
import useRequestEffect from "../../../hook/useRequestEffect";
import {getCollections} from "../store/effects";

function createData(
    name: string,
    img: string,
    floorPrice: number,
    bestOffer: number,
    d: number,
    volD: number,
    listing: number,
    ref?: any
): CellCollectionsData {
    return {name, img, floorPrice, bestOffer, d, volD, listing, ref};
}


export const RightWidget: React.FC = () => {

    const data = useStore($getCollections)
    const rows: CellCollectionsData[] = useMemo(() => {
        if (!data || !data.collections) return [];
        return data.collections.filter((ev: any) => ev.name).map((ev: any) => {
            let {name, images, sell_quote, pool_count, item_count, analytics} = ev;
            return createData(name, images.image_url, sell_quote, pool_count, analytics.volume_24_hour, analytics.volume_all_time, item_count, ev);
        })
    }, [data])

    const [cryptoSimeji, setCryptoSimeji] = useState<CellCollectionsData | undefined>();


    return (
        <RightStyle>
            <Grid container justify={"space-between"} spacing={4}>
                <Grid sm={4}>
                    <div>
                        {
                            slice(0, 5, rows).map(((ev, index) => {
                                return <div onClick={() => {
                                    setCryptoSimeji(ev)
                                }}>
                                    <CellCollectionsWidget {...ev} index={index + 1}/>
                                </div>;
                            }))
                        }
                    </div>
                </Grid>

                <Grid sm={4}>
                    <div>
                        {
                            slice(5, 10, rows).map(((ev, index) => {
                                return <div onClick={() => {
                                    setCryptoSimeji(ev)
                                }}>
                                    <CellCollectionsWidget {...ev} index={index + 6}/>
                                </div>;
                            }))
                        }
                    </div>
                </Grid>

                <Grid sm={4}>
                    <div>
                        <CryptoWidget data={cryptoSimeji || rows[0]}/>
                    </div>
                </Grid>
            </Grid>

        </RightStyle>
    );
}


const RightStyle = styled.div`


  .wap {
    background-size: 100% 100%;
  }

  .inner {
    padding: 24px;
    box-sizing: border-box;
  }


  .input {
    margin-top: 20px;
    box-sizing: border-box;


  }

  .balance {
    color: #7B7A89;
    width: 200px;

    span {
      color: #7B7A89;
    }
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .input {
      margin-top: 20px;
    }


    .charged {
      margin-bottom: 20px;
    }

    .balance {
      width: 220px;
    }
  }
`
