import React, {createContext, useCallback, useContext, useEffect, useState} from "react";
import {Modal, SwipeableDrawer} from "@material-ui/core";
import {ShoppingCart} from "../view/shopping_cart";
import {NftMateMap, PairsMap} from "../view/collection/type";
import {SudoMetaData} from "../model/sudo_meta_data";
import {NftMetadata} from "../model/nft_metadata";


export const ShoppingCartProvider: React.FC = ({children}) => {

    const [isOpen, setIsOpen] = useState(false);
    const [nftMateMap, setNftMeta] = useState<NftMateMap>({});
    const [pairsMap, setPairsItems] = useState<PairsMap>({});

    const addShopping = useCallback((mate: NftMetadata, pairs: SudoMetaData) => {
        let {uqid} = pairs;

        let _d: any = {}
        let _p: any = {}

        _d[uqid] = mate;
        _p[uqid] = pairs;

        setNftMeta({...nftMateMap, ..._d})
        setPairsItems({...pairsMap, ..._p})
    }, [nftMateMap, pairsMap])

    const deleteShopping = useCallback((uqId: string) => {
        delete pairsMap[uqId]
        setPairsItems({...pairsMap})
    }, [nftMateMap, pairsMap])


    return (
        <ShoppingCartContext.Provider value={{
            addShopping,
            show: setIsOpen,
            nftMateMap,
            pairsMap,
            deleteShopping
        }}>
            {children}
            <SwipeableDrawer
                anchor={"right"}
                open={isOpen}
                onClose={() => {
                    setIsOpen(false)
                }}
                onOpen={() => {
                }}
            >
                <ShoppingCart/>
            </SwipeableDrawer>

        </ShoppingCartContext.Provider>
    );
}

export const ShoppingCartContext = createContext<ShoppingCart>({
    show: () => {
    },
    nftMateMap: {},
    pairsMap: {},
    addShopping: (mate: NftMetadata, pairs: SudoMetaData) => {
    },
    deleteShopping: (uqId: string) => {
    }
});

interface ShoppingCart {
    show: (isOpen: boolean) => void,
    nftMateMap: NftMateMap,
    pairsMap: PairsMap,
    addShopping: (mate: NftMetadata, pairs: SudoMetaData) => void
    deleteShopping: (uqId: string) => void
}


export const useShoppingCart = () => {
    return useContext(ShoppingCartContext);
}

