import React, {useMemo} from "react";
import styled from "styled-components";
import {useCreatePairETH} from "../../server/factory";
import {useCreaterDataPool} from "../../provider/creater_lp_provider";
import {LoadingBtn} from "../../compones/loading_btn";
import {useSendModal} from "../../widget/modal/send_modal";
import {CURVEEnum} from "./type";
import {ChartsWIdget} from "./widget/charts";
import {range, scan} from "ramda";
import BigNumber from "bignumber.js";
import {EChartsOption} from "echarts-for-react/src/types";


type KlinkProps = {
    getValues: Function,
    watch: Function
}
export const KLink: React.FC<KlinkProps> = ({getValues, watch}) => {

    const handleCreatePairETH = useCreatePairETH()
    const {onSend, status,} = useSendModal(handleCreatePairETH)

    const {poolType, moralisData, curve, nftidsStateAction} = useCreaterDataPool()
    const [_curve, setCurve] = curve
    let [moralis] = moralisData
    const watchFields = watch(["spotPrice", "delta", "amount", "bonding"]);
    let [pool, _] = poolType


    const option: EChartsOption = useMemo(() => {

        let {spotPrice, delta, bonding, amount} = getValues();
        let xL = amount ? range(1, parseInt(amount)) : []
        let a: EChartsOption = {
            backgroundColor: "rgba(142, 220, 220, 0.2)",
            tooltip: {
                trigger: 'item',
                backgroundColor: 'rgba(255,255,255,0.70)',
                borderColor: '#BFC2C5',
                borderWidth: 1,
                textStyle: {
                    color: '#22252A',
                }
            },
            grid: {
                top: '4%',
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },

            xAxis: {
                type: 'category',
                data: xL
            },
            yAxis: {},
            series: [{
                name: '',
                type: 'line',
                symbol: 'circle',//拐点设置为实心
                symbolSize: 18,//拐点大小
                // animation: true,//false: hover圆点不缩放 .true:hover圆点默认缩放
                lineStyle: {
                    normal: {
                        color: '#fff',//折线的颜色
                        width: '2'//折线粗细
                    }
                },
                itemStyle: {
                    normal: {
                        color: 'rgba(0,0,0,.3)',//拐点颜色
                        borderColor: '#fff',//拐点边框颜色
                        borderWidth: 1//拐点边框大小
                    },
                    emphasis: {
                        color: '#red'//hover拐点颜色定义
                    }
                },
                areaStyle: {
                    normal: {
                        color: '#999999'//折线下面区块的颜色
                    }
                },
                smooth: true,

                data: scan((acc, elem) => {
                    if (_curve === CURVEEnum.EXPONENTIAL) {
                        console.log(new BigNumber(spotPrice).plus(Math.pow(1 + (bonding / 100), elem)).toFixed(2))
                        return new BigNumber(spotPrice).plus(Math.pow(1 + (bonding / 100), elem)).toFixed(2)
                    }
                    return acc -= delta

                }, spotPrice, xL)
            }]
        }
        return a;
    }, [watch(["spotPrice", "delta", "amount", "bonding"]), _curve]);


    return (
        <WapStyle>
            <h3 className={'tis'}>
                <p>ETH Price Per {watchFields.spotPrice} ETH.</p>
                <p>Displays how your buy price decreases with each {moralis.symbol} bought.</p>
            </h3>
            <div className="container" id="container">
                <ChartsWIdget option={option}/>
            </div>

            <div className="bottom_type">
                {/*<h3>Bonding Curve</h3>*/}
                <div className="curve">
                    <div onClick={() => setCurve(CURVEEnum.LINEAR)}
                         className={`btn ${_curve === CURVEEnum.LINEAR && 'ac'}`}>Linear Curve
                    </div>
                    <div onClick={() => setCurve(CURVEEnum.EXPONENTIAL)}
                         className={`btn ${_curve === CURVEEnum.EXPONENTIAL && 'ac'}`}>Exponential Curve
                    </div>
                </div>
            </div>

            <LoadingBtn isLoading={status} onClick={() => {
                let {spotPrice, delta, bonding} = getValues();
                let [moralis, _] = moralisData
                onSend({
                    delta,
                    bonding,
                    nftAddress: moralis.token_address,
                    poolType: pool,
                    spotPrice: spotPrice,
                    curve: _curve,
                    nftIds: nftidsStateAction[0]

                })
            }}>
                Confirm
            </LoadingBtn>

        </WapStyle>
    )
}

const WapStyle = styled.div`
  padding: 20px;

  .container {
    margin: 10px 0;
  }

  .bottom_type {
    .curve {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .btn {
        border-radius: 10px;
        background: #2C2F36;
        padding: 10px 0;
        width: 45%;
        text-align: center;
        color: #6E7075;
        cursor: pointer;

        &.ac {
          background: #52565F;
          color: #FFFFFF;
        }
      }
    }
  }

  .container {
  }

  .confirm {
    cursor: pointer;
    margin-top: 20px;
    width: 100%;
    padding: 10px 0;
    border-radius: 10px;
    background: #52565F;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;
  }

  .tis {
    padding: 10px;
  }
`
