import React, {useEffect, useState} from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {useForm} from "react-hook-form";
import {LabelInput} from "./widget/label_input";
import {KLink} from "./k_link";
import {useCreaterDataPool} from "../../provider/creater_lp_provider";
import {CURVEEnum} from "./type";

export const SunderBarPage: React.FC = () => {
    const {register, errors, watch, getValues} = useForm();
    const {moralisData, curve} = useCreaterDataPool()
    let [moralis, _] = moralisData;
    let [_curve] = curve

    const [deposit, setDeposit] = useState('--')
    const watchFields = watch(["spotPrice", "delta", "amount"]);

    useEffect(() => {
        let watchFields = watch(["spotPrice", "delta", "amount"])
        if (watchFields.spotPrice && watchFields.amount) {
            setDeposit((watchFields.spotPrice * watchFields.amount).toFixed(2))
        } else {
            setDeposit('--')
        }

    }, [watch(["spotPrice", "delta", "amount"])])

    return (
        <WapStyle>
            <CellBottomStyle container>
                <Grid item xs={12} sm={12} md={6}>
                    <div className="pt">
                        <LabelInput errors={errors}
                                    inputRef={register({
                                        required: ''
                                    })}
                                    tis={'----'}
                                    lastTis={`Floor Price: ${watchFields.spotPrice || '--'} ETH`}
                                    label={'Start Price'}
                                    name={'spotPrice'}
                                    placeholder={'0.00'}
                                    unit={"ETH"}
                        />
                    </div>


                    <div className="pt">
                        {
                            _curve == CURVEEnum.LINEAR ? <>
                                <LabelInput errors={errors}
                                            inputRef={register({
                                                required: ''
                                            })}
                                            tis={'----'}
                                            label={'Delta'}
                                            name={'delta'}
                                            placeholder={'0.00'}
                                            unit={"ETH"}
                                            onChange={() => {
                                            }}
                                />
                            </> : <>
                                <LabelInput errors={errors}
                                            inputRef={register({
                                                required: ''
                                            })}
                                            tis={'----'}
                                            label={'Bonding Curve'}
                                            name={'bonding'}
                                            placeholder={'0.00'}
                                            unit={"%"}
                                            onChange={() => {
                                            }}
                                />
                            </>
                        }

                    </div>

                    <div className="pt">
                        <LabelInput errors={errors}
                                    inputRef={register({
                                        required: 'amount'
                                    })}
                                    label={'Amount'}
                                    name={'amount'}
                                    placeholder={'0'}
                        >
                            <div className="btn_max">
                                {moralis.symbol}
                            </div>
                        </LabelInput>
                        <p className='bottom_msg'>You will need to deposit {deposit} ETH total.</p>
                    </div>
                </Grid>
                <Grid item xs={12} sm={12} md={6}>
                    <KLink watch={watch} getValues={getValues}/>
                </Grid>
            </CellBottomStyle>

        </WapStyle>
    );
}

const WapStyle = styled.div`


  .MuiPaper-root {
    background: transparent;
  }

  .MuiAccordionSummary-root {
    padding: 0 20px;
  }

  .MuiButtonBase-root {
    background: #161515;
    border-radius: 10px;
    height: 78px;
    border: 1px solid transparent;

    .MuiButtonBase-root {
      background: transparent;
    }
  }

  .MuiAccordionSummary-root.Mui-expanded {
    background: #262525;
    border: 1px solid #A19F9F;
  }

  .MuiAccordionDetails-root {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .MuiButtonBase-root, .MuiAccordionSummary-root.Mui-expanded {
      height: 240px;
    }
  }
`

const CellBottomStyle = styled(Grid)`
  padding: 10px 0;
  box-sizing: border-box;
  margin-top: 8px;


  .pt {
    padding: 5px 30px;
    box-sizing: border-box;

    .btn_max {
      background-color: #2d2f35;
      padding: 4px 10px;
      border-radius: 100px;
      font-size: 18px;
      font-weight: 400;
      color: #FFFFFF;
      cursor: pointer;
    }

    .bottom_msg {
      font-size: 14px;
      font-weight: 400;
      color: #FFFFFF;
      margin-bottom: 20px;
    }
  }

  .tis {
    font-size: 16px;
    font-weight: 500;
    color: #BABAC8;
    padding: 10px;
    box-sizing: border-box;
    line-height: 20px;

    p {
      margin-bottom: 5px;
    }
  }

  .bottom_text {
    padding: 0 30px 20px;
    box-sizing: border-box;
    color: #FE9000;
    font-size: 12px;
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .tis {
      padding: 20px;
      font-size: 14px;
    }
  }
`
