import React, { useEffect } from "react";
import { Line } from '@antv/g2plot';

const data = [
    { Date: '1', rating: 1 },
    { Date: '2', rating: 2 },
    { Date: '3', rating: 3 },
    { Date: '4', rating: 4 },
    { Date: '5', rating: 5 },
];

export const KLink = () => {

    useEffect(() => {
        setTimeout(() => {

            const line = new Line('container', {
                data,
                padding: 'auto',
                xField: 'Date',
                yField: 'rating',
                reflect: 'y',
                height: 300,
                xAxis: {
                    // because coordinate is reflectY
                    position: 'top',
                },
            });

            line.render();
        }, 200)
    }, [])

    return (
        <>
            <h3 className={'tis'}>
                <p>ETH Price Per Azuki</p>
                <p>Displays how your buy price decreases with each Azuki bought.</p>

                <div className="container" id="container"></div>
            </h3>
        </>
    )
}
