import React, {} from "react";
import {TextField} from "@material-ui/core";
import styled from "styled-components";
import indexEth from "../../../assets/images/index_eth.png"

interface InputInterFace {
    label: string,
    placeholder: string,
    inputRef: React.Ref<any>,
    errors: any;
    name?: string,
    type?: string,
}


export const LabelInput: React.FC<InputInterFace> = ({
                                                         label,
                                                         placeholder,
                                                         inputRef,
                                                         errors,
                                                         name,
                                                         type,
                                                     }) => {


    return (
        <>
            <LabelStyle>
                <p className={'left'}>
                    {label}

                </p>
                <p className='right'>
                </p>


            </LabelStyle>

            <FlexStyle>
                <InputStyle>
                    <TextField name={name || label}
                               error={Boolean(errors[name || label])}
                               placeholder={placeholder}
                               unselectable={'off'}
                               fullWidth={true}
                               inputRef={inputRef}
                               type={type}
                    />
                    <img src={indexEth} alt=""/>
                </InputStyle>


            </FlexStyle>
            <HelperTextStyle>
                <p className={'err'}>{errors[name || label] ? errors[name || label].message : ''}</p>
            </HelperTextStyle>
        </>
    );
}

