import {useCallback, useEffect, useState} from "react";
import {compose, flatten, indexBy, map, mapObjIndexed, path, prop} from "ramda";
import {NftMateMap, PairsMap} from "../type";
import {HttpServer} from "../../../net/http_server";
import {useParams} from "react-router-dom";
import BigNumber from "bignumber.js";


export const usePairs = () => {
    const [nftMeta, setNftMeta] = useState<NftMateMap>({});
    const [pairsItems, setPairsItems] = useState<PairsMap>({})
    let {id} = useParams<{ id: string, type: string }>();


    const handeGetMateData = useCallback(async (map: PairsMap, nftMeta: NftMateMap) => {
        mapObjIndexed(async (value, key) => {
            let _meta: any = await HttpServer.alchemyapi(id, value.nftId || '0')
            nftMeta[key] = _meta;
            setNftMeta(nftMeta)
        }, map)
    }, [pairsItems, id])

    const handleGetPairs = useCallback(async () => {

        let result: any = await HttpServer.thegraphGetNft(id);

        let list = path<any[]>(['data', 'collection', 'pairs'], result) || [];
        list = list.filter((ev: any) => ev.nftIds.length > 0);

        let permissions = compose(
            flatten,
            map((ev: any) => {
                return ev.nftIds.map((i: string) => ({...ev,
                    nftId: i,
                    uqid: `${ev.id}_${i}`,
                    averagePrice: new BigNumber(ev.spotPrice).plus(ev.delta).plus(ev.fee).toString()
                }))
            }),
        )(list)
        let permissionMap = indexBy(prop('uqid'), permissions);
        setPairsItems(permissionMap)
        handeGetMateData(permissionMap, nftMeta);
    }, [id])

    useEffect(() => {
        handleGetPairs()
    }, [])


    return {pairsItems, nftMeta}

}
