import React, {useMemo} from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {useSendModal} from "../../../widget/modal/send_modal";
import {NftPoolProps} from "../index";
import {LoadingBtn} from "../../../compones/loading_btn";
import {NftCardItem} from "../../inventory/widget/nft_card_item";
import {useActivity} from "../hook/useNftMeta";
import {values} from "ramda";
import {useWithdrawERC721} from "../../../server/pair";

type NftSelectModelProps = {
    onDismiss: Function
} & NftPoolProps
export const NftWithdrawModel: React.FC<NftSelectModelProps> = ({sudo}) => {

    const handleWithdrawERC721 = useWithdrawERC721(sudo?.id);
    const {status, onSend} = useSendModal(handleWithdrawERC721)
    const {nftMeta} = useActivity(sudo)
    let idsMap: any = {}
    return (
        <WapStyle>
            <div className="model_title">Withdraw NFT</div>
            <CellBottomStyle container>
                <Grid className={'nft_wap'} container spacing={2}>
                    {
                        sudo.nftIds.map((ev: string, index: number) => {
                            let key = `${sudo.nft}_${ev}`
                            let metadata = nftMeta[key]

                            return <Grid item xs={4} md={3} key={key}>
                                <NftCardItem
                                    data={{
                                        id: sudo.nft,
                                        imgUrl: metadata?.metadata.image,
                                        nftId: ev,
                                        nftIds: [],
                                        symbol: metadata?.contractMetadata.symbol,
                                        hidePrice: true,
                                        isSelect: false
                                    }}
                                    onCb={(select: boolean) => {
                                        if (select) {
                                            idsMap[key] = ev;
                                        } else {
                                            delete idsMap[key]
                                        }
                                    }}
                                />
                            </Grid>
                        })
                    }
                </Grid>

            </CellBottomStyle>

            <div className={'bottom'}>
                <LoadingBtn isLoading={status} onClick={() => {
                    let ids = values(idsMap);
                    onSend({
                        ids: ids,
                        nftAddress:sudo.nft
                    })
                }}>
                    <h4>Withdraw NFT</h4>
                </LoadingBtn>
            </div>
        </WapStyle>
    )
}

