import React, {useMemo, useState} from "react";
import styled from "styled-components";
import Roy from "../../../assets/images/question-mark.svg"


type NftBuyItemProps = {
    data: {
        id: string,
        nftIds: any[],
        nftId?: string,
        spotPrice?: string,
        imgUrl?: string,
        symbol?: string,
        isSelect?: boolean,
        hidePrice?:boolean
    },
    onCb: Function,
}
export const NftCardItem: React.FC<NftBuyItemProps> = ({data, onCb}) => {

    const [select, setSelect] = useState(data.isSelect)

    return (
        <NftBuyItemStyle style={{borderColor: select ? "#fff" : "#404040"}} onClick={() => {
            onCb(!select)
            setSelect(!select)
        }}>
            <div className="top">
                <div className="nft_img" style={{backgroundImage: `url(${data.imgUrl || Roy})`}} />
            </div>
            <div className="bottom">

                <>
                    <p className={'nftId'}>{data.symbol}</p>
                    <p className={'sud_nftId'}>#{data.nftId}</p>
                </>

                {!data.hidePrice && <div className="bar">
                    No offer
                </div>}
            </div>


        </NftBuyItemStyle>
    );
}

const NftBuyItemStyle = styled.div`
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid;


  .bottom {
    box-sizing: border-box;

    p {
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
    }

    .nftId {
      font-style: normal;
      font-size: 14px;
      line-height: 14px;
      letter-spacing: -0.1em;
      margin: 4px 0;
      font-weight: bold;
      text-transform: Capitalize;
      color: #FFFFFF;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;

    }

    .sud_nftId {
      font-style: normal;
      font-weight: 400;
      font-size: 8px;
      align-items: center;
      letter-spacing: -0.1em;
      color: #FFFFFF;
    }

    .bar {
      margin-top: 5px;
      padding: 5px 0;
      background-color: #404040;
      border-radius: 100px;
      text-align: center;
      color: white;

    }
  }

  .top {
    border-radius: 6px;
    overflow: hidden;

    .nft_img {
      display: block;
      width: 100%;
      height: 140px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

`
