import React, {useCallback, useContext, useMemo, useState} from "react";
import styled from "styled-components";
import {EnsNameWidget} from "./widget/ens_name";
import {WidthStyle} from "../../widget/style/width_style";
import {Grid, TextField} from "@material-ui/core";
import {MoralisData} from "../../model/moralis_data";
import {NftCardItem} from "./widget/nft_card_item";
import * as R from "ramda";
import Roy from "../../assets/images/question-mark.svg";
import InventorySelectAc from "../../assets/images/inventory_select_ac.png"
import {LoadingWidget} from "../../widget/base/loading_widget";
import {ModalContext} from "../../provider/model_provider";
import {SellWidget} from "./sell";
import {useStore} from "effector-react";
import {$getMoralisFileData} from "./store";
import {getMoralisFileData} from "./store/effects";
import {FullNaData} from "../../widget/base/na_data";


export const InventoryMain: React.FC = () => {
    const data = useStore($getMoralisFileData)
    const loading = useStore(getMoralisFileData.pending)

    const [checkeds, setChecked] = useState<MoralisData[]>([]);

    const group: any = useMemo(() => {
        if (!data || !data.result) {
            return []
        }
        return R.compose(
            R.values,
            R.groupBy((student: MoralisData) => {
                return student.token_address;
            }),
            R.path(['result'])
        )(data);
    }, [data])


    const checkedMoralis = useCallback(() => {
        let _data = R.compose(
            R.filter((student: MoralisData) => {
                return student.isChecked;
            }),
            R.path(['result'])
        )(data);
        setChecked(_data)
    }, [data])


    const {onPresent} = useContext(ModalContext);


    if (loading || !data.result) {
        return <LoadingWidget/>;
    }

    if (!data?.result.length) {
        return <FullNaData>empty</FullNaData>;
    }
    return (
        <InventoryStyle>
            <EnsNameWidget/>
            <div className="body">

                <LeftItemStyle>
                    <h4>Only Show Offers </h4>

                    <div className="input">
                        <TextField
                            unselectable={'off'}
                            fullWidth={true}
                        />
                    </div>

                    {
                        group.map((ev: MoralisData[]) => {
                            let item: MoralisData = ev[0];
                            return <div className={'name'} key={item.token_address}>
                                <img src={InventorySelectAc} alt=""/>
                                <p>
                                    {item.name}
                                </p>
                                <span>{
                                    ev.length
                                }</span>

                            </div>;
                        })
                    }
                </LeftItemStyle>

                <Grid className={'nft_wap'} container spacing={2}>

                    {
                        data?.result.map((ev: MoralisData, index: number) => {
                            let metadata = JSON.parse(ev.metadata)
                            if (!metadata) {
                                metadata = {
                                    name: ev.name,
                                    image: Roy
                                }
                            }
                            return <Grid item xs={4} md={3} key={`${ev.name}_${ev.token_id}`}>
                                <NftCardItem
                                    data={{
                                        id: ev.token_address,
                                        imgUrl: metadata.image || metadata.image_url,
                                        nftId: ev.token_id,
                                        nftIds: [],
                                        symbol: metadata.name || ev.name
                                    }}
                                    onCb={(select: boolean) => {
                                        data.result[index]['isChecked'] = select;
                                        checkedMoralis()
                                    }}
                                />
                            </Grid>
                        })
                    }
                </Grid>

            </div>

            <div className="footer">
                <div className="your">
                    Your Pools
                </div>
                <div className="list" onClick={() => {
                    onPresent(<SellWidget nfts={checkeds}/>)
                }}>
                    List {checkeds.length} NTFS
                </div>
            </div>
        </InventoryStyle>
    );
}

const LeftItemStyle = styled.div`
  width: 20%;

  .name {
    padding: 13px 10px;
    background: #242424;
    border-radius: 8px;
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
      display: block;
      width: 14px;
    }

    p {
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      display: flex;
      align-items: center;
      letter-spacing: -0.14em;
      color: #FFFFFF;
      flex: 1;
      padding: 0 10px;
    }

    span {
      display: inline-block;
      padding: 4px 5px;
      border-radius: 5px;
      color: white;
      background-color: #000;
    }
  }


  .input {
    margin-top: 20px;
    background: #080808;
    border: 1px solid #8D909A;
    border-radius: 10px;
    padding: 4px 10px;
    box-sizing: border-box;
  }

  .MuiInputBase-root {
    color: white;
  }

  .MuiInput-underline:before, .MuiInput-underline:after {
    display: none;
  }

  .MuiInputBase-input {
    font-family: 'ZX', sans-serif;

    &::-webkit-input-placeholder {
      text-align: start;
    }
  }

  h4 {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    display: flex;
    align-items: center;
    letter-spacing: -0.14em;
    color: #8D909A;
  }
`


