import React, {useCallback, useState} from "react";
import styled from "styled-components";
import {Grid, Modal} from "@material-ui/core";
import {useForm} from "react-hook-form";
import {LabelInput} from "./widget/label_input";
import {KLink} from "./k_link";
import {CollectionsDataModel} from "../../model/collections_data";
import {SudoMetaData} from "../../model/sudo_meta_data";
import {PoolItemTitle} from "../collection/widget/pool_item_title";
import {PoolType} from "../collection/pool/nft_pool_item";
import {NftPoolItemHeader} from "../collection/widget/nft_pool_item_header";
import {NFTBalanceInput} from "./widget/nft_balance_input";
import {TokenBalanceInput} from "./widget/token_balance_input";
import ethImg from "../../assets/images/pool_eth.png";
import {UpDataPoolContext} from "../../provider/updata_pool_provicer";
import {RootMyPoolData} from "../../model/my_pool_data";
import {BodyStyle, ModalBodyStyle, ModalContext} from "../../provider/model_provider";
import {getBalanceFormat} from "../../utils/formatBalance";
import {CellBottomStyle, UpdateModel} from "./model/update_model";
import {NftSelectModel} from "./model/nft_deposit_model";
import {UpdataLoadingBtn} from "./widget/loading_btn";
import {HttpStatus} from "../../utils/wait_fn";
import {BalanceInput} from "./widget/balance_input";
import {NftWithdrawModel} from "./model/nft_withdraw_model";

export type NftPoolProps = {
    collections: CollectionsDataModel,
    sudo: RootMyPoolData,
    type: PoolType,
}
export const NftPoolModel: React.FC<NftPoolProps> = (props) => {

    let {type} = props


    return (
        <UpDataPoolContext.Provider value={{
            collections: props.collections,
            sudo: props.sudo
        }}>
            <NftPollStyle>
                <div className="model_title">
                    <PoolItemTitle type={type}/>
                </div>
                <CellWidget {...props}/>
            </NftPollStyle>
        </UpDataPoolContext.Provider>

    )
}

const NftPollStyle = styled.div`
  width: 90%;
  margin: 0 auto;
  background-color: #191b1f;
  border-radius: 20px;
  border: 1px solid #2C2F36;
  padding: 0 20px;
  box-sizing: border-box;

  .model_title {
    text-align: center;
    padding: 17px 0;
    border-bottom: 2px solid #8D909A;
    display: flex;
    justify-content: center;


  }

`

const CellWidget: React.FC<NftPoolProps> = ({collections, sudo, type}) => {
    const {register, errors} = useForm();
    let {name, images: {image_url},} = collections
    const {id, owner, spotPrice, delta} = sudo;
    const [isOpen, setIsOpen] = useState(false);
    const [_type, setType] = useState(0);
    const handleDismiss = useCallback(() => {
        setIsOpen(false);
        setType(0)
    }, [setIsOpen]);



    return (
        <WapStyle>
            <CellBottomStyle container>
                <Grid item md={6} xs={12}>

                    <NftPoolItemHeader id={id} name={name} image_url={image_url} ownerId={owner}/>
                    <div onClick={() => {
                        setIsOpen(true)
                    }}>
                        <div className="pt">
                            <LabelInput errors={errors}
                                        inputRef={register({})}
                                        lastTis={'Floor Price: 1.000 ETH'}
                                        label={'Current Price'}
                                        name={'Price'}
                                        placeholder={getBalanceFormat(spotPrice, 4)}
                            >
                                <img className={'ethImg'} src={ethImg} alt=""/>
                            </LabelInput>
                        </div>
                        <div className="pt">
                            <LabelInput errors={errors}
                                        inputRef={register({
                                            required: 'Please enter a number > 0'
                                        })}
                                        label={'Delta'}
                                        name={'Delta'}
                                        placeholder={getBalanceFormat(delta, 4)}
                                        unit={"%"}
                            />
                        </div>
                    </div>
                    <div className="pt">
                        <NFTBalanceInput>
                            <>
                                <UpdataLoadingBtn isLoading={HttpStatus.success} onClick={() => {
                                    setIsOpen(true);
                                    setType(1)
                                }}>
                                    <p className={'deposit'}>Deposit</p>
                                </UpdataLoadingBtn>
                                <UpdataLoadingBtn isLoading={HttpStatus.success} onClick={() => {
                                    setIsOpen(true);
                                    setType(2)
                                }}>
                                    <p className={'deposit'}>Withdraw</p>
                                </UpdataLoadingBtn>
                            </>
                        </NFTBalanceInput>
                    </div>
                    <div className="pt">
                        <TokenBalanceInput/>
                    </div>
                </Grid>
                <Grid item md={6}>
                    <KLink/>
                </Grid>
            </CellBottomStyle>
            <Modal
                open={isOpen}
                onClose={handleDismiss}
                aria-labelledby="spring-modal-title"
                aria-describedby="spring-modal-description"
            >
                <ModalBodyStyle>
                    <div className={'bg'} onClick={handleDismiss}/>
                    <BodyStyle>
                        {
                            _type === 0 &&
                            <UpdateModel sudo={sudo} collections={collections} type={type} onDismiss={handleDismiss}/>
                        }


                        {
                            _type === 1 && <NftSelectModel sudo={sudo} collections={collections} type={type}
                                                           onDismiss={handleDismiss}/>
                        }  {
                            _type === 2 && <NftWithdrawModel sudo={sudo} collections={collections} type={type}
                                                           onDismiss={handleDismiss}/>
                        }
                    </BodyStyle>
                </ModalBodyStyle>
            </Modal>
        </WapStyle>

    );
}


const WapStyle = styled.div`


  .MuiPaper-root {
    background: transparent;
  }

  .MuiAccordionSummary-root {
    padding: 0 20px;
  }

  .MuiButtonBase-root {
    background: #161515;
    border-radius: 10px;
    height: 78px;
    border: 1px solid transparent;

    .MuiButtonBase-root {
      background: transparent;
    }
  }

  .MuiAccordionSummary-root.Mui-expanded {
    background: #262525;
    border: 1px solid #A19F9F;
  }

  .MuiAccordionDetails-root {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .MuiButtonBase-root, .MuiAccordionSummary-root.Mui-expanded {
      height: 240px;
    }
  }
`
