import React, {useCallback, useContext, useEffect, useMemo, useState} from "react";
import styled from "styled-components";
import {WidthStyle} from "../../widget/style/width_style";
import {HttpStatus} from "../../utils/wait_fn";
import {Grid, TextField} from "@material-ui/core";
import * as R from "ramda";
import {EnsNameWidget} from "../inventory/widget/ens_name";
import {NftPoolItem} from "../collection/pool/nft_pool_item";
import {RootMyPoolData} from "../../model/my_pool_data";
import {useCollections} from "./hook/useCollections";
import {defaultCollections} from "./type";
import {LoadingWidget} from "../../widget/base/loading_widget";
import {titleText} from "../collection/widget/pool_item_title";
import {TypeSelectionWidget} from "../inventory/widget/type_selection";
import {BondingList, usePool} from "./hook/usePool";
import {mergeDeepWithKey} from "ramda";
import {useHistory} from "react-router-dom";


export const MyPoolMain: React.FC = () => {
    const {push} = useHistory()
    const [filters, setFilters] = useState<any>({
        type: {"1": false, "2": false, "0": false},
        curve: {
            [BondingList.linear]: true,
            [BondingList.exponetial]: true,
        }
    });

    const {
        request,
        status,
        pools,
        productionFeeding,
    } = usePool(filters)

    const {handeGetCollection, collectionMap} = useCollections()

    const groupPools = useMemo(() => {
        return R.groupBy((ev: RootMyPoolData) => ev.type)(pools);
    }, [pools])

    const groupCurvePools = useMemo(() => {
        return R.groupBy((ev: RootMyPoolData) => ev.bondingCurve)(pools);
    }, [pools])

    const getData = useCallback(async () => {
        let data = await request('');
        let _groupData = R.compose(
            R.keys,
            R.groupBy((student: RootMyPoolData) => {
                return student.nft;
            }), R.path(['data', 'newPairs']),)(data);
        handeGetCollection(_groupData)
    }, [handeGetCollection])


    const getCollection = useCallback((id: string) => {
        if (collectionMap[id]) {
            return collectionMap[id];
        }
        return defaultCollections;
    }, [collectionMap])

    useEffect(() => {
        getData()
    }, [])


    if (status === HttpStatus.wait) {
        return <LoadingWidget/>;
    }


    return (
        <InventoryStyle>
            <div className={"title"}>
                <EnsNameWidget/>
                <div className={'btn'}
                     onClick={() => {
                         push('/lp')
                         // onPresent(<CreaterLP />);
                     }}
                >New Position
                </div>
            </div>
            <div className="body">

                <LeftItemStyle>
                    <h4>Only Show Offers </h4>

                    <div className="input">
                        <TextField
                            unselectable={'off'}
                            fullWidth={true}
                        />
                    </div>

                    {
                        Object.keys(groupPools).map((pools) => {
                            return <div key={`${pools}_`} onClick={() => {
                                let _data = mergeDeepWithKey(
                                    (k, l, r) => k === 'type' ? R.concat(l, r) : r,
                                    filters,
                                    {
                                        type: {
                                            [pools]: !filters['type'][pools]
                                        },
                                    }
                                )
                                setFilters(_data);
                            }}>
                                <TypeSelectionWidget
                                    name={titleText[parseInt(pools)]}
                                    value={`${groupPools[pools].length}`}
                                    isSelect={filters['type'][pools]}
                                />
                            </div>;
                        })
                    }
                    <div style={{height: "20px"}}/>
                    <h4>Bonding Curve</h4>
                    {
                        Object.keys(BondingList).map((key) => {
                            let _address = BondingList[key];
                            return <div key={`${key}_`} onClick={() => {
                                let _data = mergeDeepWithKey(
                                    (k, l, r) => k === 'type' ? R.concat(l, r) : r,
                                    filters,
                                    {
                                        curve: {
                                            [_address]: !filters['curve'][_address]
                                        },
                                    }
                                )
                                setFilters(_data);
                            }}>
                                <TypeSelectionWidget
                                    name={key.toUpperCase()}
                                    value={`${groupCurvePools[_address.toLowerCase()]?.length}`}
                                    isSelect={filters['curve'][_address]}
                                />
                            </div>;
                        })
                    }
                </LeftItemStyle>

                <Grid className={'nft_wap'} container spacing={2}>
                    {productionFeeding.map((ev: RootMyPoolData) =>
                        <Grid key={`${ev.id}`} item md={4} xs={12} xl={4} sm={6}>
                            <NftPoolItem props={{
                                type: parseInt(ev.type),
                                sudoData: ev,
                                collections: getCollection(ev.nft),
                            }}/>
                        </Grid>)}
                </Grid>
            </div>

        </InventoryStyle>
    );
}

const LeftItemStyle = styled.div`
  width: 20%;


  .input {
    margin-top: 20px;
    background: #080808;
    border: 1px solid #8D909A;
    border-radius: 10px;
    padding: 4px 10px;
    box-sizing: border-box;
  }

  .MuiInputBase-root {
    color: white;
  }

  .MuiInput-underline:before, .MuiInput-underline:after {
    display: none;
  }

  .MuiInputBase-input {
    font-family: 'ZX', sans-serif;

    &::-webkit-input-placeholder {
      text-align: start;
    }
  }

  h4 {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    display: flex;
    align-items: center;
    letter-spacing: -0.14em;
    color: #8D909A;
  }
`


const InventoryStyle = styled.div`
  ${() => WidthStyle.MinWidth};
  padding-top: 40px;
  box-sizing: border-box;
  max-width: 100%;

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
    box-sizing: border-box;

    .btn {
      padding: 10px 20px;
      box-sizing: border-box;
      background: #FFFFFF;
      border: 2px solid #000000;
      border-radius: 12px;
      cursor: pointer;
    }
  }

}

.body {
  padding-bottom: 40px;

  .nft_wap {
    width: 80%;
    border-radius: 20px;
    padding: 10px;
    box-sizing: border-box;
  }


  max-width: 96%;
  margin: 0 auto;
  justify-content: space-between;
  padding-top: 30px;
  display: flex;


}

`
