import React from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {LabelInput} from "./widget/label_input";
import {useForm} from "react-hook-form";
import {MoralisData} from "../../model/moralis_data";
import Roy from "../../assets/images/question-mark.svg";
import {NftCardItem} from "./widget/nft_card_item";

type SellProps = {
    nfts: MoralisData[]
}
export const SellWidget: React.FC<SellProps> = ({nfts}) => {
    const {register, errors} = useForm();
    return (
        <SellStyle>
            <div className="sell_left">
                <h4>Collections</h4>
            </div>
            <div className="right">
                <h3 className="title">You are Listing 6 Azuki Nfts</h3>
                <div className="box">
                    <Grid container spacing={2}>
                        {
                            nfts.map((ev: MoralisData, index: number) => {
                                let metadata = JSON.parse(ev.metadata)
                                if (!metadata) {
                                    metadata = {
                                        name: ev.name,
                                        image: Roy
                                    }
                                }
                                return <Grid item xs={4} md={4} key={`${ev.name}_${ev.token_id}`}>
                                    <NftCardItem
                                        data={{
                                            id: ev.token_address,
                                            imgUrl: metadata.image || metadata.image_url,
                                            nftId: ev.token_id,
                                            nftIds: [],
                                            symbol: metadata.name || ev.name
                                        }}
                                        onCb={(select: boolean) => {

                                        }}
                                    />
                                </Grid>
                            })
                        }
                    </Grid>
                </div>
                <div className="bottom">
                    <Grid md={6} xs={8}>
                        <LabelInput errors={errors}
                                    inputRef={register({
                                        required: 'Please enter a number > 0'
                                    })} label={'Start Price:'}
                                    placeholder={"1.0"}
                        />

                        <LabelInput errors={errors}
                                    inputRef={register({
                                        required: 'Please enter a number > 0'
                                    })} label={'Price Increase:'}
                                    placeholder={"1.0"}
                        />

                        <div className="confirm">
                            Confirm
                        </div>
                    </Grid>
                </div>
            </div>
        </SellStyle>
    );
}


