export const HeaderWidget: React.FC<{ collections: CollectionsDataModel }> = ({
                                                                                  collections
                                                                              }) => {


    const {onPresent} = useContext(ModalContext);


    if(!collections) return <div />
    let {
        name,
        address,
        images: {image_url, banner_image_url},
        analytics: {volume_all_time}
    } = collections
    return (
        <HeaderStyle bgImg={banner_image_url}>
            <img src={banner_bg} alt="" className="banner_bg"/>

        <div className="inner">
            <img src={image_url || nftImg} onError={() => nftImg} alt="" className="img"/>
            <div className="right">
                <div className="top">
                    <p className={'name'}>{name}</p>
                    <p className={'address'}>{FormatAddr(address)}</p>
                </div>
                <div className="liquidity">
                    <div className="item">
                        <h3><img src={indexEth} alt=""/>{volume_all_time.toFixed(3)}</h3>
                        <h4>Total Volume</h4>
                    </div>

                    <div className="item">
                        <h3><img src={indexEth} alt=""/>10.0K</h3>
                        <h4>Floor Price</h4>
                    </div>

                    <div className="item">
                        <h3><img src={indexEth} alt=""/>10.0K</h3>
                        <h4>Best Offer</h4>
                    </div>

                    <div className="btn" onClick={()=>{
                        onPresent(<ListEntireModel />);
                    }}>
                        List Entire Collection
                    </div>
                </div>
            </div>
        </div>


        </HeaderStyle>
    );
}

