import React from "react";
import styled from "styled-components";
import indexAccout from "../../../assets/images/index_accout.png"
import indexEth from "../../../assets/images/index_eth.png"
import {CellCollectionsData} from "../type";
import {getBalanceFormat} from "../../../utils/formatBalance";
import TableRow from "@material-ui/core/TableRow";
import {useHistory} from "react-router-dom";

const CellWidget: React.FC<{ label: string, value: string }> = ({label, value}) => {
    return (
        <CellStyle>
            <h4>{label}</h4>
            <h5><img src={indexEth} alt=""/>{value}</h5>
        </CellStyle>
    );
}

const CellStyle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;

  h4 {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    color: white;
  }

  h5 {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: white;
    letter-spacing: 0;

    img {
      display: block;
      width: 20px;
      margin-right: 5px;
    }
  }
`

export const CryptoWidget: React.FC<{ data?: CellCollectionsData }> = ({data}) => {
    const history = useHistory();

    if (!data) {
        return <div/>
    }
    return (
        <CryptoStyle>
            <img src={data.img} alt="" className="icon"/>
            <h3 className="btn" onClick={() => {
                history.push(`/collection/buy/${data.ref.address}`)
            }}>CryptoSimeji</h3>
            <CellWidget value={getBalanceFormat(data.floorPrice)} label={'Floor price'}/>
            <CellWidget value={getBalanceFormat(data.bestOffer)} label={'Best  Offer'}/>
            <CellWidget value={`${data.volD.toFixed(2)}`} label={'Volume'}/>
            <CellStyle>
                <h4>Change</h4>
                <h5 style={{color: "#4FCFA9"}}>+73 %</h5>
            </CellStyle>
            <CellStyle>
                <h4>Listings</h4>
                <h5>12</h5>
            </CellStyle>

            <CellWidget value={"2.13"} label={'TVL'}/>
        </CryptoStyle>
    );
}

const CryptoStyle = styled.div`
  background: linear-gradient(0deg, rgba(58, 58, 58, 0.35), rgba(58, 58, 58, 0.35)), url(小格子.png);
  border: 1px solid #FFFFFF;
  border-radius: 20px;
  padding: 12px 40px;
  box-sizing: border-box;

  .icon {
    display: block;
    width: 80px;
    margin: 0 auto;
  }

  .btn {
    width: 55%;
    margin: 15px auto;
    background: #FFFFFF;
    border: 1px solid #000000;
    border-radius: 12px;
    text-align: center;
    font-size: 16px;
    color: #080808;
    padding: 13px 0;
    font-weight: 400;
    cursor: pointer;

  }
`
